<template>
    <div class="liquidity-provider-deposit-options">
        <!-- <div class="window light trigger-block" @click="triggerDropdown">
            <label style="color: #EAEAEA;">{{$localizeFilter(`${options[selectedIndex].replace('-','_')}`)}}</label>
            <button class="trigger-button">
                <img style="width:10px; height: 6px;" :src="require('@/assets/newImages/white_arrow.png')">
            </button>
        </div> -->
        <!-- v-if="isOpen" -->
        <div class="window light dropdown-block">
        <ul>
            <li v-for="(option,idx) in options" :key="idx" :class="selectedIndex==idx ? 'selected' : ''" @click="selectOption(idx)">
                {{$localizeFilter(`${option.replaceAll('-','_')}`)}}
                <div class="option-balance" v-if="option==='from-wallet'">{{$localizeFilter('dropdown_balance',[$utils.formatNumber(walletBalance, 'en-US', 6, 6)])}}</div>
                <div class="option-balance" v-if="option==='from-bonus-account'">{{$localizeFilter('dropdown_balance',[$utils.formatNumber(bonusAccountBalance, 'en-US', 6, 6)])}}</div>
            </li>
        </ul>
        </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
    props: [
        // 'options'
    ],
    data: ()=>({
        isOpen: false,
        selectedIndex: null,
        options:["from-wallet","from-bonus-account","from-external-wallet"],
        walletBalance: 0,
        bonusAccountBalance: 0
    }),
    setup() {
        
    },
    methods: {
        triggerDropdown() {
            this.isOpen = !this.isOpen
        },
        selectOption(idx) {
            this.selectedIndex = idx
            this.$emit('selectOption', this.options[idx])
            this.$emit('close')
            // this.isOpen = false
        }
    },
    async mounted() {
        try {
            const { walletBalance, bonusAccountBalance } = await this.$store.dispatch('getLiquidityProviderDepositBalance')
            this.walletBalance = walletBalance / 1000000.0
            this.bonusAccountBalance = bonusAccountBalance / 1000000.0
        } catch (e) {
            // this.$error(e)
        }
    }
})
</script>

